// UI 
import React from 'react';
import PropTypes from 'prop-types';
// import { LazyImg } from '@ifeng/ui_pc_lazyLoad';
import { getCommonList } from '../service';
import { formatComment } from '@src/public/utils/format';
// icon
import applause from './assets/applause.png';
import slapFace from './assets/slapFace.png';
import thumbsUp from './assets/thumbsUp.png';
// 
import Interaction from './components/interaction';
// 
import classNames from 'classnames';
import styles from './index.css';
import Avatar from './assets/defaultAvatar.png';

const cx = classNames.bind(styles);
// 
const imgList = {
    img_1: 'https://x0.ifengimg.com/ucms/2022_10/2145BC4462D2424F8F6999A887B520087B38BA61_size183_w360_h360.png',
    img_2: 'https://x0.ifengimg.com/ucms/2022_10/A9D1F088D591328B589AAE212942D99C6DA8D231_size150_w360_h360.png',
    img_3: 'https://x0.ifengimg.com/ucms/2022_10/9BA1D8D104F23597F6FE866A3C01A0A9DBCE0F62_size145_w360_h360.png',
    img_4: 'https://x0.ifengimg.com/ucms/2022_10/859D93C49FFB62198DCD9A5F6FD4A4FC12788B8B_size146_w360_h360.png',
    img_5: 'https://x0.ifengimg.com/ucms/2022_10/A275D4274D20584C405D4B184095636B5A13CC1D_size95_w360_h360.png',
};

class Danmu extends React.Component {
    /* */
    constructor(props) {
        super(props);
        this.state = {
            // UI(5)
            list: [],
            // (95)
            list2: [],
            // 
            list3: [],
            current: null,
            step: 0,
            totalY: 0,
            translateY: 0,
            toggle: true, // 
            isStop: false, // 
            intervalId: null, // 
            isIntervalId: false, // 
            pauseAni: false, // 
            siteAdd: 0, // 
            isOpacity: true, // 
            // showDanmu: this.props.showDanmu,
            isBrand: this.props.isBrand,
            dmId: '',
            normalAvater: imgList['img_1'],
            itemHeight: 0, // 
            itemMarginTop: 0, // 
        };
        // 
        this.listRef = React.createRef();
        this.listWrapRef = React.createRef();
        this.inputRef = React.createRef();
        this.siteRef = React.createRef(); // 
        this.scrollTimer = React.createRef(null);
        // 
        this.count = this.count.bind(this);
    }

    // ui
    uiSource(source) {
        // const source = response.nodes;
        if (source) {
            for (let i = 0; i < source.length; i++) {
                source[i].isShow = false;
                source[i].type = '';
                // 
                source[i].interaction = [
                    {
                        state: 'thumbsUp',
                        amount: Math.ceil(Math.random() * 10),
                        icon: 'thumbsUp',
                    },
                    {
                        state: 'applause',
                        amount: Math.ceil(Math.random() * 10),
                        icon: 'applause',
                    },
                    {
                        state: 'slapFace',
                        amount: Math.ceil(Math.random() * 10),
                        icon: 'slapFace',
                    },
                ];
            }

            this.setState(state => ({
                list: (state.list = source),
            }));
        }
    }

    // 
    dataSource(source) {
        // const source = response.nodes;
        try {
            for (let i = 0; i < source.length; i++) {
                source[i].isShow = false;
                source[i].type = '';
                source[i].interaction = [
                    {
                        state: 'thumbsUp',
                        amount: Math.ceil(Math.random() * 10),
                        icon: 'thumbsUp',
                    },
                    {
                        state: 'applause',
                        amount: Math.ceil(Math.random() * 10),
                        icon: 'applause',
                    },
                    {
                        state: 'slapFace',
                        amount: Math.ceil(Math.random() * 10),
                        icon: 'slapFace',
                    },
                ];
            }

            this.setState(state => ({
                list2: (state.list2 = source),
            }));
        } catch (e) {
            console.log(e);
        }
    }

    componentDidMount() {
        // itemHeight
        this.setItemHeight();

        const { danmu } = this.props;

        if (danmu) {
            // 
            const firstFive = danmu?.slice(0, 5);
            const lastFive = danmu?.slice(5, 100);

            // UI
            this.uiSource(firstFive);

            // 
            this.dataSource(lastFive);

            // 
            this.setState(state => ({
                list3: [...danmu],
            }));

            // 
            setTimeout(() => {
                this.count();
            }, 0);

            return;
        } else {
            try {
                getCommonList({ id: this.props.id, size: 50 }).then(res => {
                    // 
                    const firstFive = res?.slice(0, 5);
                    const lastFive = res?.slice(5, 100);
                    // UI

                    this.uiSource(firstFive);
                    // 
                    this.dataSource(lastFive);
                    // 
                    this.setState(state => ({
                        list3: [...res],
                    }));
                    // 
                    this.count();
                });
            } catch (e) {
                console.error(e);
            }
        }

        // // /
        window.document.addEventListener('visibilitychange', () => {
            if (document.hidden) {
                this.stop();
            } else {
                // this.reset();
                this.count(this.state.translateY);
            }
        });
    }

    componentDidUpdate() {
        if (!this.state.isOpacity) {
            console.log('');
            this.stop();
        }
    }

    componentWillUnmount() {
        console.log(this.scrollTimer.current);
        window.cancelAnimationFrame(this.scrollTimer.current);
    }

    // componentWillReceiveProps(nextProps) {
    //     var showDanmu = nextProps.showDanmu;

    //     //  props.email  state
    //     this.hangleToggle();
    // }

    count(props) {
        if (window.requestAnimationFrame) {
            // list
            // 
            window.cancelAnimationFrame(this.scrollTimer.current);

            // 
            if (!this.state.list.length) {
                return;
            }

            // 
            const listWrapRef = this.listWrapRef.current;
            // 
            const listWrapRefHeight = listWrapRef.offsetHeight;

            // 
            const el = this.listRef.current;

            // 
            const childNodes = el.childNodes;
            // 
            const itmeHeight = childNodes[1].offsetHeight + parseInt(getComputedStyle(childNodes[1]).marginTop, 10);
            // 
            // var computedStyle = getComputedStyle(childNodes[1], null);
            // var itemMarginBottom = parseInt(computedStyle.marginBottom, 10);

            const translateY = Math.floor(props) || 0;
            let start = -1;

            const step = timestamp => {
                if (start === -1) {
                    start = timestamp;
                }

                // 
                const listHeight = el.offsetHeight;

                // elapsed:ms,
                const elapsed = timestamp - start;

                // translateY: / 48ms1px =  = 
                const distance = Math.floor(elapsed / 48);

                // 
                el.style.transform = `translateY(-${Math.floor(translateY + distance)}px)`;

                //  translateY
                this.setState(state => ({
                    translateY: (state.translateY = translateY + distance),
                }));

                //  = =
                if (listHeight - (translateY + distance) < listWrapRefHeight) {
                    // console.log("");

                    // BA 
                    this.setState(
                        state => ({
                            list: state.list.concat(this.state.list2[0]),
                        }),
                        () => {
                            // 
                            childNodes[childNodes.length - 1].setAttribute('class', 'item show');
                        },
                    );

                    // A
                    this.setState(state => ({
                        list: state.list.slice(1),
                    }));

                    // B
                    this.setState(state => ({
                        list2: state.list2.slice(1),
                    }));

                    // BC
                    if (this.state.list2.length === 0) {
                        this.setState(state => ({
                            list2: [...state.list2, ...state.list3],
                        }));
                    }

                    // +1()item(+)
                    // this.state.siteAdd = this.state.siteAdd + itmeHeight + itemMarginBottom;
                    this.state.siteAdd = this.state.siteAdd + itmeHeight;
                    if (this.siteRef.current) {
                        this.siteRef.current.style.height = `${this.state.siteAdd}px`;
                    }
                }

                // : /
                for (let i = 0; i < childNodes.length; i++) {
                    // 
                    if (childNodes[i].getBoundingClientRect().top <= listWrapRef.getBoundingClientRect().top) {
                        // site
                        if (childNodes[i].getAttribute('class') === 'site') {
                            // console.log('');
                        } else {
                            childNodes[i].setAttribute('class', 'item hide');
                        }

                        // => 
                    } else if (
                        childNodes[i].getBoundingClientRect().top <=
                            listWrapRefHeight + listWrapRef.getBoundingClientRect().top &&
                        childNodes[i].getBoundingClientRect().top >
                            listWrapRefHeight + listWrapRef.getBoundingClientRect().top - 1
                    ) {
                        // childNodes[i].setAttribute("class", "item show");
                        // itemclass
                        // childNodes[childNodes.length-1].setAttribute("class", "item show");
                    }
                }

                // 
                this.scrollTimer.current = window.requestAnimationFrame(step);
            };

            this.scrollTimer.current = window.requestAnimationFrame(step);
        }
    }

    // 
    start() {
        console.log('');
        this.count();
    }

    // 
    stop() {
        window.cancelAnimationFrame(this.scrollTimer.current);
    }

    // 
    reset() {
        console.log('');
        if (window.requestAnimationFrame) {
            // list
            // 
            window.cancelAnimationFrame(this.scrollTimer.current);
            // 
            if (!this.state.list.length) {
                return;
            }

            // 
            const listWrapRef = this.listWrapRef.current;
            // 
            const listWrapRefHeight = listWrapRef.offsetHeight;

            // 
            const el = this.listRef.current;

            // 
            const childNodes = el.childNodes;
            // 
            const itmeHeight = childNodes[1].offsetHeight + parseInt(getComputedStyle(childNodes[1]).marginTop, 10);
            // 
            const computedStyle = getComputedStyle(childNodes[1], null);
            const itemMarginBottom = parseInt(computedStyle.marginBottom, 10);

            let start = -1;
            let totalY = 0;
            const step = timestamp => {
                const translateY = this.state.translateY;

                if (start === -1) {
                    start = timestamp;
                }

                // 
                const listHeight = el.offsetHeight;

                // elapsed:ms,
                const elapsed = timestamp - start;

                // translateY: / 48ms1px =  = 
                const distance = elapsed / 48;

                totalY = translateY + distance;

                // console.log("totalY",totalY);
                // translateY
                // this.setState(state => ({
                //   translateY:state.translateY = translateY + distance
                // }))

                // 
                el.style.transform = `translateY(-${totalY}px)`;

                //  = =
                if (listHeight - totalY < listWrapRefHeight) {
                    // console.log("");

                    // BA 
                    this.setState(
                        state => ({
                            list: state.list.concat(this.state.list2[0]),
                        }),
                        () => {
                            // 
                            childNodes[childNodes.length - 1].setAttribute('class', 'item show');
                        },
                    );

                    // A
                    this.setState(state => ({
                        list: state.list.slice(1),
                    }));

                    // B
                    this.setState(state => ({
                        list2: state.list2.slice(1),
                    }));

                    // BC
                    if (this.state.list2.length === 0) {
                        this.setState(state => ({
                            list2: [...state.list2, ...state.list3],
                        }));
                    }

                    // +1()item(+)
                    this.state.siteAdd = this.state.siteAdd + itmeHeight + itemMarginBottom;
                    if (this.siteRef.current) {
                        this.siteRef.current.style.height = `${this.state.siteAdd}px`;
                    }
                }

                // : /
                for (let i = 0; i < childNodes.length; i++) {
                    // 
                    if (childNodes[i].getBoundingClientRect().top <= listWrapRef.getBoundingClientRect().top) {
                        // site
                        if (childNodes[i].getAttribute('class') === 'site') {
                            // console.log('');
                        } else {
                            childNodes[i].setAttribute('class', 'item hide');
                        }

                        // => 
                    } else if (
                        childNodes[i].getBoundingClientRect().top <=
                            listWrapRefHeight + listWrapRef.getBoundingClientRect().top &&
                        childNodes[i].getBoundingClientRect().top >
                            listWrapRefHeight + listWrapRef.getBoundingClientRect().top - 1
                    ) {
                        // childNodes[i].setAttribute("class", "item show");
                        // itemclass
                        // childNodes[childNodes.length-1].setAttribute("class", "item show");
                    }
                }

                // 
                this.scrollTimer.current = window.requestAnimationFrame(step);
            };

            this.scrollTimer.current = window.requestAnimationFrame(step);
        }
    }

    // 
    hangleToggle() {
        // 
        this.setState(
            state => ({
                toggle: !state.toggle,
            }),
            () => {
                // 
                // 
                const el = this.listRef.current;
                // const listWrap = document.getElementsByClassName("listWrap")[0];

                // console.log(el.style.opacity);

                if (!this.state.toggle) {
                    // el.style.opacity = 0;
                    el.style.visibility = 'hidden';
                    // this.stop()
                    // // 
                    // listWrap.style.opacity = 0;
                    // this.setState((state) => ({
                    //   isOpacity: false,
                    // }));
                } else {
                    el.style.visibility = 'visible';
                    // el.style.opacity = 1;
                    // this.reset();
                    // // 
                    // listWrap.style.opacity = 1;
                    // this.setState((state) => ({
                    //   isOpacity: true,
                    // }));
                }
            },
        );
    }

    // ID
    clearDmId() {
        // id
        this.setState(state => ({
            dmId: (state.dmId = ''),
        }));
    }

    // 
    onDanmu(dmId, e) {
        // 
        const list = this.state.list;
        // const list2 = JSON.parse(JSON.stringify(list));
        const list2 = list;

        // 
        const current = list2.filter(item => item.commentId === dmId)[0];

        // DOM
        const event = e.target;

        // 
        const isBrand = this.props.isBrand;

        if (isBrand) {
            return;
        }

        // 
        if (event.closest('.hide')) {
            // console.log('hide');
            return;
        }
        // ID
        this.setState(state => ({
            dmId: (state.dmId = dmId),
        }));

        // 
        if (dmId && this.state.dmId === dmId) {
            // 
            this.count(this.state.translateY);
            this.setState(state => ({
                dmId: (state.dmId = ''),
            }));
        } else if (dmId && this.state.dmId !== dmId) {
            // 
            this.stop();
        }

        // 
        for (let i = 0; i < list2.length; i++) {
            // 
            if (list2[i].commentId === dmId) {
                list2[i].isShow = !list2[i].isShow;
            } else {
                // 
                list2[i].isShow = false;
            }
        }

        // 
        this.setState(state => ({
            current: (state.current = current),
            isIntervalId: (state.isIntervalId = true),
        }));

        // 
        this.setState(
            state => ({
                list: (state.list = list2),
            }),
            () => {},
        );

        // :
        this.danmuEventListener();
    }

    // :
    danmuEventListener() {
        // 
        const list = this.state.list;
        const list2 = list;

        // 
        const danmuWrapper = document.getElementById('danmuWrapper');

        // 
        danmuWrapper.style.pointerEvents = 'auto';

        danmuWrapper.addEventListener('click', () => {
            // console.log('');
            // 
            this.count(this.state.translateY);
            this.setState(state => ({
                dmId: (state.dmId = ''),
            }));

            // 
            for (let i = 0; i < list2.length; i++) {
                list2[i].isShow = false;
            }
            // 
            danmuWrapper.style.pointerEvents = 'none';

            return;
        });
    }

    // icon
    onAni(name) {
        // type
        const current = this.state.current;

        this.setState(state => ({
            current: (state.current.type = name),
        }));

        // 
        const list = this.state.list;
        const list2 = list;

        const currentItem = list2.filter(item => item.commentId === current.commentId)[0];

        currentItem.type = name;
        currentItem.isShow = !currentItem.isShow;
        // 
        this.setState(
            state => ({
                current: (state.current = null),
                dmId: (state.dmId = ''), // ID
            }),
            () => {},
        );

        // 
        this.count(this.state.translateY);

        // 
        for (let i = 0; i < list2.length; i++) {
            list2[i].isShow = false;
        }
    }

    // icon
    getDanmu(name) {
        let url = '';

        switch (name) {
            case 'applause':
                url = applause;
                break;
            case 'slapFace':
                url = slapFace;
                break;
            case 'thumbsUp':
                url = thumbsUp;
                break;
            default:
                break;
        }

        return url;
    }

    // javascript html/
    checkHtml(htmlStr) {
        const reg = /<[^>]+>/g;

        return reg.test(htmlStr);
    }

    // 
    randomNum = (...arg) => {
        switch (arg.length) {
            case 1:
                return parseInt(Math.random() * arg[0] + 1, 10);
            case 2:
                return parseInt(Math.random() * (arg[1] - arg[0] + 1) + arg[0], 10);
            default:
                return 0;
        }
    };
    // 
    randomAavter = url => {
        const num = this.randomNum(1, 5);

        if (url) {
            return url;
        } else {
            return this.state.normalAvater;
        }
    };
    // itemHeight
    setItemHeight = () => {
        // 
        // px=>rem
        const itemHeightRem = 64 / 75; // 6475237.51
        const itemMarginTopRem = 12 / 75; // 1275237.51
        // htmlfont-size
        const htmlFontSize = parseFloat(document.getElementsByTagName('html')[0].style.fontSize, 10);

        // rem=>rpx
        const itemHeightPx = Math.floor(itemHeightRem * htmlFontSize);
        const itemMarginTopPx = Math.floor(itemMarginTopRem * htmlFontSize);

        // alert(`${itemHeightPx }-----${ itemMarginTopPx}`);
        this.setState(state => {
            state.itemHeight = itemHeightPx;
            state.itemMarginTop = itemMarginTopPx;
        });
    };

    render() {
        // UI
        const { list, onhidden, onshow, normalAvater, itemHeight, itemMarginTop } = this.state;
        const doubleList = [...list];

        return (
            <div className={styles.wrapper} id="danmuWrapper">
                <div className={styles.danmu}>
                    {/*  */}
                    <div className={styles.listWrap} ref={this.listWrapRef}>
                        <div className={styles.list} ref={this.listRef} style={{ opacity: 1 }}>
                            <div className="site" ref={this.siteRef} />

                            {doubleList?.length
                                ? doubleList.map((item, index) => {
                                      return (
                                          <div
                                              key={item.commentId}
                                              // key={index}
                                              className={cx({
                                                  item: true,
                                              })}
                                              style={{ height: `${itemHeight}px`, marginTop: `${itemMarginTop}px` }}>
                                              {/*  */}
                                              <Interaction item={item} onClick={state => this.onAni(state)} />

                                              <div
                                                  className={styles.content}
                                                  onClick={this.onDanmu.bind(this, item.commentId)}>
                                                  {/*  */}
                                                  {/* <LazyImg
                                                      // src={item?.avatar || normalAvater || Avatar}
                                                      src={this.randomAavter(item?.avatar)}
                                                      className={styles.itemheadImg}
                                                  /> */}
                                                  <img  src={this.randomAavter(item?.avatar)}
                                                      className={styles.itemheadImg} />

                                                  {this.checkHtml(formatComment(item.content)) ? (
                                                      <div
                                                          className={styles.itemText}
                                                          dangerouslySetInnerHTML={{
                                                              __html: formatComment(item.content),
                                                          }}
                                                      />
                                                  ) : (
                                                      <div
                                                          className={styles.itemText}
                                                          style={{
                                                              height: `${itemHeight}px`,
                                                              lineHeight: `${itemHeight}px`,
                                                          }}>
                                                          {item.content}
                                                      </div>
                                                  )}

                                                  {item.type ? (
                                                      <img
                                                          src={this.getDanmu(item.type)}
                                                          className={styles.itemheadImg}
                                                          alt=""
                                                      />
                                                  ) : null}
                                              </div>
                                          </div>
                                      );
                                  })
                                : null}
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

Danmu.propTypes = {
    // showDanmu: PropTypes.bool,
    isBrand: PropTypes.bool,
    id: PropTypes.string,
    danmu: PropTypes.array,
};

export default Danmu;
