let timestampIndex = 0;

const loaded = {};

/**
 * js
 * @param  {String} url     
 * @param  {Object} options 
 *   @param {Object} data 
 *   encodeURIComponent
 *   @param {Number || Boolean} cache false
 *   @param {Number} timeout jsonp60000
 *   @param {Boolean} reload   truejquery false
 */
export default function(
    url,
    { data = {}, cache = `${new Date().valueOf()}${timestampIndex++}`, timeout = 60000, reload = true } = {},
) {
    if (reload === false && loaded[url]) {
        return loaded[url];
    }

    const load = new Promise((resolve, reject) => {
        const timer = setTimeout(() => {
            if (reload === false) {
                delete loaded[url];
            }
            reject(new Error(`timeout|${timeout}`));
        }, timeout);

        // 
        const params = [];

        // 
        if (typeof data === 'object') {
            for (const key of Object.keys(data)) {
                params.push(`${encodeURIComponent(key)}=${encodeURIComponent(data[key])}`);
            }
        } else {
            reject(new Error('typeError|data'));

            return;
        }

        if (cache) {
            params.push(`_=${cache}`);
        }

        let fullUrl = '';

        if (url.includes('?')) {
            fullUrl = `${url}&${params.join('&')}`;
        } else {
            fullUrl = `${url}?${params.join('&')}`;
        }

        const insertScript = document.createElement('script');

        insertScript.src = fullUrl;

        insertScript.onload = insertScript.onreadystatechange = function onreadystatechange() {
            if (!this.readyState || this.readyState === 'loaded' || this.readyState === 'complete') {
                resolve({ type: 'success', msg: '' });
                clearTimeout(timer);
                insertScript.onload = insertScript.onreadystatechange = null;
                const head = document.getElementsByTagName('head')[0];

                if (head) {
                    head.removeChild(insertScript);
                }
            }
        };

        insertScript.onerror = function onerror() {
            if (reload === false) {
                delete loaded[url];
            }
            clearTimeout(timer);
            reject(new Error(`loadScriptError|:${url}`));
        };

        const head = document.getElementsByTagName('head')[0];

        if (head) {
            head.appendChild(insertScript);
        }
    });

    if (reload === false && !loaded[url]) {
        loaded[url] = load;
    }

    return load;
}
