/**
 * 
 * @param {Function} fn 
 * @param {Number} time  fn 300
 * @returns {Function} functionfunction
 */
export default function throttle(fn, time = 300) {
    let timer = null;

    // this
    return function throttleHandler(...args) {
        if (timer === null) {
            //  fn(...args)this fn(...args)  fn.apply(undefined, args);
            fn.apply(this, args);
            timer = setTimeout(() => {
                timer = null;
            }, time);
        }
    };
}
