import { likeCommentAxios as request } from '@src/public/utils/request';

/**
 * 
 * @param {string} appId  appId: 10
 * @param {string} subjectId id
 * @param {number} businessType  like: 1 | dingcai: 2 | flower: 3 | candle: 4
 * @param {string} opType INSERT | DEL   | 
 * @returns
 */
export const likeCommentApi = (appId, subjectId, businessType, opType) => {
    return request.post('/likeComment', {
        body: {
            appId,
            subjectId,
            businessType,
        },
        opType,
    });
};
