/**
 * 
 * @param {Function} fn 
 * @param {Number} time 300
 * @returns {Function} functionfunction
 */
export default function debounce(fn, time = 300) {
    let timer = null;

    // this
    return function debounceHandler(...args) {
        if (timer != null) {
            clearTimeout(timer);
        }
        timer = setTimeout(() => {
            //  fn(...args)this fn(...args)  fn.apply(undefined, args);
            fn.apply(this, args);
            timer = null;
        }, time);
    };
}
