import React, { useEffect, useRef, useState } from 'react';
import classNames from 'classnames/bind';
import ReactDOM from 'react-dom';
import PropTypes from 'prop-types';
import { useModalOpen } from '@src/public/hooks/useModalOpen';
import styles from './index.css';

const cx = classNames.bind(styles);

const Wrap = ({ children, top, onClose, show, dragToClose }) => {
    useModalOpen();

    const touchStartPositionRef = useRef(0);
    const touchMoveDiffRef = useRef(0);
    const touchMovingRef = useRef(false);
    const scrolledRef = useRef(false);
    const diffLimitRef = useRef(0);

    const contentRef = useRef();
    const scrollRef = useRef();

    const handleTouchStart = e => {
        // 
        const { targetTouches } = e;
        const touch = targetTouches[0];
        // y
        const { screenY } = touch;

        // refy
        touchStartPositionRef.current = screenY;
        // reftrue
        touchMovingRef.current = true;
        // 
        contentRef.current.style.transition = 'unset';

        const scrollTop = scrollRef.current.scrollTop;

        scrolledRef.current = scrollTop > 0;

        if (!diffLimitRef.current) {
            const winWidth = Math.min(window.innerWidth, window.__IFENG_ENV__.maxDocWidth);

            diffLimitRef.current = Math.floor((278 * winWidth) / 750);
        }
    };

    const handleTouchMove = e => {
        const scrollTop = scrollRef.current.scrollTop;

        if (scrollTop > 0) {
            scrolledRef.current = true;
        }

        if (scrolledRef.current) {
            return;
        }
        // 
        if (touchMovingRef.current) {
            // reftruefalse
            const { targetTouches } = e;
            const touch = targetTouches[0];
            // y
            const { screenY } = touch;
            // yy
            const diff = screenY - touchStartPositionRef.current;

            if (diff > 0) {
                // 0
                // overflowhidden
                scrollRef.current.style.overflowY = 'hidden';
                // 
                contentRef.current.style.transform = `translate3d(0px, ${diff}px, 0px)`;
                touchMoveDiffRef.current = diff;

                if (diff >= diffLimitRef.current) {
                    // 
                    onClose();
                    // reffalse
                    touchMovingRef.current = false;
                }
            }
        }
    };

    const handleTouchEnd = () => {
        // 
        if (touchMovingRef.current) {
            // reftrue
            // 
            // 

            // overflowscroll
            scrollRef.current.style.overflowY = 'scroll';
            // reffalse
            touchMovingRef.current = false;
            if (touchMoveDiffRef.current < diffLimitRef.current) {
                // 
                // transitiontransitiontransition
                contentRef.current.style.transition = 'transform 0.3s ease';
                // 
                // setTranslateY(0);
                contentRef.current.style.transform = 'translate3d(0px, 0px, 0px)';
                // y0
                touchStartPositionRef.current = 0;
                touchMoveDiffRef.current = 0;
            }
        }
    };

    return (
        <div
            className={cx('mask', { maskShow: show, maskHide: !show })}
            onClick={e => {
                e.stopPropagation();
                onClose();
            }}
            onTouchMove={e => e.stopPropagation()}>
            <div
                className={cx('content', {
                    contentShow: show,
                    contentHide: !show,
                })}
                style={{ top }}
                onClick={e => {
                    e.stopPropagation();
                }}
                {...(dragToClose
                    ? {
                          onTouchStart: handleTouchStart,
                          onTouchMove: handleTouchMove,
                          onTouchEnd: handleTouchEnd,
                      }
                    : {})}
                ref={contentRef}>
                <div className={styles.scroll} ref={scrollRef}>
                    {children}
                </div>
            </div>
        </div>
    );
};

Wrap.propTypes = {
    children: PropTypes.any,
    top: PropTypes.number,
    onClose: PropTypes.func,
    show: PropTypes.bool,
    dragToClose: PropTypes.bool,
};

const Sheet = ({ show, children, top, onClose, dragToClose = true }) => {
    const [showState, setShowState] = useState(show);

    useEffect(
        () => {
            if (show) {
                setShowState(show);
            } else {
                setTimeout(() => {
                    setShowState(show);
                }, 300);
            }
        },
        [show],
    );

    return showState
        ? ReactDOM.createPortal(
              <Wrap top={top} onClose={onClose} show={show} dragToClose={dragToClose}>
                  {children}
              </Wrap>,
              document.body,
          )
        : null;
};

export default Sheet;
