import dayjs from 'dayjs';
import duration from 'dayjs/plugin/duration';
import emojiConf from '@src/public/constants/emoji';

// dayjs Duration 
dayjs.extend(duration);

// 
export const formatNodeTime = (timestamp, format) => {
    const now = dayjs.unix(timestamp);

    if (format) {
        return now.format(format);
    }

    const today = dayjs(dayjs().format('YYYY-MM-DD'));
    const yesterday = today.subtract(1, 'day');
    const tomorrow = today.add(1, 'day');

    const nowMs = now.valueOf();
    const todayMs = today.valueOf();
    const yesterdayMs = yesterday.valueOf();
    const tomorrowMs = tomorrow.valueOf();

    if (nowMs >= todayMs && nowMs < tomorrowMs) {
        return now.format('HH:mm');
    } else if (nowMs >= yesterdayMs && nowMs < todayMs) {
        return ` ${now.format('HH:mm')}`;
    } else {
        return now.format('YYYY-MM-DD');
    }
};

// 
export const formatNodeTimeEnum = (timestamp, type) => {
    const typeList = new Map([[1, ''], [2, 'YYYY'], [3, 'YYYYM'], [4, 'YYYY-MM-DD'], [5, 'YYYY-MM-DD HH:mm']]);

    return formatNodeTime(timestamp, typeList.get(type));
};

// 
export const formatPeopleCount = count => {
    if (count > 99999999) {
        return `${(count / 100000000).toFixed(1)}`;
    } else if (count > 9999) {
        return Number((count / 10000).toFixed(1)) === 10000 ? '9999.9' : `${(count / 10000).toFixed(1)}`;
    } else {
        return count;
    }
};

// 
// 2021-07-10 13:00:33
// 24XX
// 124XX
// 1XX
// 1
export const formatCommentTime = date => {
    const currentTime = dayjs();
    const targetTime = dayjs(date);
    const ms = targetTime.isAfter(currentTime)
        ? dayjs.duration(targetTime.diff(currentTime))
        : dayjs.duration(currentTime.diff(targetTime));
    const days = Math.floor(ms.asDays());
    const hours = Math.floor(ms.asHours());
    const minutes = Math.floor(ms.asMinutes());

    if (hours >= 24) {
        return `${days}`;
    } else if (hours > 0) {
        return `${hours}`;
    } else if (minutes > 0) {
        return `${minutes}`;
    } else {
        return '';
    }
};

// 
export const formatComment = comment => {
    const reg = /\[([^[\]]+)]/g;

    return comment.replace(reg, (match, p1) => {
        const emoji = emojiConf.find(item => item.code === `[${p1}]`);

        if (emoji) {
            return `<img style="width:0.5rem;vertical-align:text-bottom;" src="${`https://x0.ifengimg.com/fe/circle/emoji/${p1}.png`}" alt="${p1}" />`;
        }

        return match;
    });
};
