//  - easeOutSine
export const easing = distance => {
    // t: current time, b: begInnIng value, c: change In value, d: duration
    const t = distance;
    const b = 0;
    const d = window.screen.availHeight; // 
    const c = d / 2.5; // 

    return c * Math.sin((t / d) * (Math.PI / 2)) + b;
};
