import createXhr from './utils/createXhr';

let timestampIndex = 0;

/**
 * ajaxpromise
 * @param {String} url 
 * @param {Object} options 
 *   @param {Object} body body
 *   @param {Object} query url
 *   encodeURIComponent
 *   @param {String} type ajax   get get | post
 *   @param {Number || Boolean} cache false
 *   @param {Number} timeout 60000
 *   @param {Boolean} async true
 *   @param {Boolean} corsCookie cookietruefalsetrue*
 */
export default function(
    url,
    {
        query = {},
        body = {},
        type = 'get',
        cache = `${new Date().valueOf()}${timestampIndex++}`,
        timeout = 60000,
        async = true,
        corsCookie = false,
    } = {},
) {
    return Promise.race([
        new Promise((resolve, reject) => {
            const xhr = createXhr();

            // 
            const params = [];

            if (typeof query === 'object') {
                for (const key of Object.keys(query)) {
                    params.push(`${encodeURIComponent(key)}=${encodeURIComponent(query[key])}`);
                }
            } else {
                reject({
                    code: 'queryError',
                    tips: '',
                    msg: { msg: '', content: query },
                    status: xhr.status,
                });

                return;
            }

            if (typeof body !== 'object') {
                reject({
                    code: 'bodyError',
                    tips: '',
                    msg: { msg: '', content: body },
                    status: xhr.status,
                });

                return;
            }

            if (cache) {
                params.push(`_=${cache}`);
            }

            if (url.includes('?')) {
                url = `${url}&${params.join('&')}`;
            } else {
                url = `${url}?${params.join('&')}`;
            }

            if (corsCookie) {
                xhr.withCredentials = true;
            }

            xhr.open(type, url, async);

            if (type.toLocaleLowerCase() === 'post') {
                xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');
            }
            xhr.onreadystatechange = () => {
                if (xhr.readyState === 4) {
                    if ((xhr.status >= 200 && xhr.status < 300) || xhr.status === 304) {
                        try {
                            const result = new Function(`return ${xhr.responseText || '{}'}`)();

                            if (typeof result === 'object') {
                                resolve(result);
                            } else {
                                reject({
                                    code: 'typeError',
                                    tips: '',
                                    msg: { msg: '', content: xhr.responseText },
                                    status: xhr.status,
                                });
                            }
                        } catch (e) {
                            reject({
                                code: 'parseError',
                                tips: '',
                                msg: { msg: '', content: xhr.responseText },
                                status: xhr.status,
                            });
                        }
                    } else {
                        try {
                            const result = new Function(`return ${xhr.responseText}`)();

                            if (typeof result === 'object') {
                                reject(result);
                            } else {
                                reject({
                                    code: 'typeError',
                                    tips: '',
                                    msg: { msg: '', content: xhr.responseText },
                                    status: xhr.status,
                                });
                            }
                        } catch (e) {
                            reject({
                                code: 'parseError',
                                tips: '',
                                msg: { msg: '', content: xhr.responseText },
                                status: xhr.status,
                            });
                        }
                    }
                }
            };
            xhr.send(type.toLocaleLowerCase() === 'get' ? null : JSON.stringify(body));
        }),
        new Promise((resolve, reject) => {
            setTimeout(() => {
                reject({
                    code: 'timeout',
                    tips: '',
                    msg: { msg: '', content: timeout },
                });
            }, timeout);
        }),
    ]);
}
