import { setAppQuery } from '@src/public/utils/url';
import { linkAnalyze } from './service';
import { linkType } from './contants';

export const generateUcmsLink = (type, base62) => {
    switch (type) {
        case 'slide':
            return `https://nine.ifeng.com/GetNewsDocs?aid=ucms_${base62}`;
        case 'special':
            return `https://nine.ifeng.com/newTopic?topicid=${base62}&json=y`;
        case 'hotEvent':
            return `https://nine.ifeng.com/hotSpotDetailList?eventName=${base62}`;
        default:
            return `ucms_${base62}`;
    }
};

export const groundsDispatch = async ({ to, type, needLinkAnalyze }) => { // to:, typeweb, needLinkAnalyze :false
    let dispatchUrl = to;
    let dispatchType = type;

    // web
    if (dispatchType === 'web' && needLinkAnalyze) {
        try {
            // 
            const link = dispatchUrl.startsWith('http') ? dispatchUrl : `http://${dispatchUrl}`;
            // 
            const res = await linkAnalyze(link);
            const { code, data } = res;

            if (code === 0 && data) {
                // outLinkType121
                const { outLinkType, doc, base62 } = data;

                if (outLinkType === 1 && doc) {
                    const { type: ucmsType } = doc;

                    if (linkType[ucmsType]) {
                        dispatchUrl = generateUcmsLink(ucmsType, base62);
                        dispatchType = linkType[ucmsType];
                    }
                }
            }
        } catch (e) {
            console.error(e);
        }
    }

    // setAppQuery:URL?immersion=true
    if (dispatchType === 'web') {
        dispatchUrl = setAppQuery(dispatchUrl);
    }

    window.grounds.dispatch(dispatchType, dispatchUrl);
};
