import { useState, useEffect } from 'react';
import dayjs from 'dayjs';
import { jsonp } from '@ifeng/ui_base';
import { isGrounds } from '@src/public/utils/app';
import { QRCODE_PREFIX, QRCODE_VERSION } from '@src/public/constants';

export const getQrCode = async ({ url, short = true }) => {
    // 
    const callbackNameShort = 'getShortCallback';
    // 
    const callbackNameQrcode = 'getQrCodeCallback';

    // 
    let newImg = '';

    // qrcodelocalStoragekey
    const key = `${QRCODE_PREFIX}${url}`;

    try {
        // keylocalStorage
        const qrocodeItem = localStorage.getItem(key);

        // 
        if (qrocodeItem) {
            // 
            const qrcodeData = JSON.parse(qrocodeItem);

            // linkexpiresms
            const { link, expires, version } = qrcodeData;

            // linkexpires
            if (link && expires && version === QRCODE_VERSION) {
                // 
                const isExpired = dayjs().isAfter(dayjs(expires));

                if (!isExpired) {
                    // 
                    newImg = link;
                }
            }
        }
    } catch (e) {
        console.error(e);
    }

    if (!newImg) {
        // 
        // 
        let shortUrl = '';

        if (short) {
            try {
                const res = await jsonp('https://s.ifeng.com/api/create', {
                    data: {
                        url,
                        callback: callbackNameShort,
                    },
                    jsonCallback: callbackNameShort,
                    cache: false,
                });

                const { data } = res;
                const { shorturl } = data;

                shortUrl = shorturl;
            } catch (e) {
                console.error(e);
            }
        }

        // 
        const res = await jsonp('https://qrcode.ifeng.com/qrcode.php', {
            data: {
                url: shortUrl || url,
                callback: callbackNameQrcode,
            },
            jsonCallback: callbackNameQrcode,
            cache: false,
        });

        const { qrcode_url } = res;

        if (qrcode_url) {
            // 
            newImg = `https:${qrcode_url}`;
            try {
                const qrcodeData = {
                    link: newImg,
                    expires: dayjs()
                        .add(7, 'day')
                        .valueOf(),
                    version: QRCODE_VERSION,
                };

                localStorage.setItem(key, JSON.stringify(qrcodeData));
            } catch (e) {
                console.error(e);
            }
        }
    }

    return newImg;
};

export const useQrCodeUrl = ({ url, short = true, filter = () => isGrounds }) => {
    // state
    const [img, setImg] = useState('');

    useEffect(
        () => {
            // filter
            const filterResult = filter();

            if (filterResult) {
                getQrCode({ url, short }).then(data => {
                    setImg(data);
                });
            }
        },
        [url, short, filter],
    );

    return img;
};

