import reportQuestion from './utils/reportQuestion';

import createXhr from './utils/createXhr';

let timestampIndex = 0;

/**
 * ajaxpromise
 * @param {String} url 
 * @param {Object} options 
 *   @param {Object} data 
 *   encodeURIComponent
 *   @param {String} type ajax   get get | post
 *   @param {String} dataType ajax  json json | text
 *   @param {Number || Boolean} cache false
 *   @param {Number} timeout 60000
 *   @param {Number} benchmarkTime api1000
 *   @param {Boolean} isReportQuestion 
 *   @param {Boolean} async true
 */
export default function(
    url,
    {
        data = {},
        type = 'get',
        dataType = 'json',
        cache = `${new Date().valueOf()}${timestampIndex++}`,
        timeout = 60000,
        async = true,
    } = {},
) {
    return Promise.race([
        new Promise((resolve, reject) => {
            const xhr = createXhr();

            // 
            const params = [];

            if (typeof data === 'object') {
                for (const key of Object.keys(data)) {
                    params.push(`${encodeURIComponent(key)}=${encodeURIComponent(data[key])}`);
                }
            } else {
                reject(new Error('typeError|data'));

                return;
            }

            const caches = [];

            if (cache) {
                caches.push(`_=${cache}`);
            }

            if (type.toLocaleLowerCase() === 'get') {
                if (url.includes('?')) {
                    url = `${url}&${[...params, ...caches].join('&')}`;
                } else {
                    url = `${url}?${[...params, ...caches].join('&')}`;
                }
            } else if (url.includes('?')) {
                url = `${url}&${caches.join('&')}`;
            } else {
                url = `${url}?${caches.join('&')}`;
            }

            xhr.open(type, url, async);
            if (type.toLocaleLowerCase() === 'post') {
                xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');
            }
            xhr.onreadystatechange = () => {
                if (xhr.readyState === 4) {
                    if ((xhr.status >= 200 && xhr.status < 300) || xhr.status === 304) {
                        reportQuestion(url);
                        switch (dataType) {
                            case 'json':
                                try {
                                    const result = new Function(`return ${xhr.responseText}`)();

                                    if (typeof result === 'object') {
                                        resolve(result);
                                    } else {
                                        reject(
                                            new Error(
                                                `typeError|json |${encodeURIComponent(xhr.responseText)}`,
                                            ),
                                        );
                                    }
                                } catch (e) {
                                    reject(
                                        new Error(`typeError|json |${encodeURIComponent(xhr.responseText)}`),
                                    );
                                }
                                break;
                            default:
                                resolve(xhr.responseText);
                        }
                    } else {
                        reject(new Error(`requestError||${xhr.status}`));
                    }
                }
            };
            xhr.send(type.toLocaleLowerCase() === 'get' ? null : params.join('&'));
        }),
        new Promise((resolve, reject) => {
            setTimeout(() => {
                reject(new Error(`timeout|${timeout}`));
            }, timeout);
        }),
    ]);
}
