export default class Event {
    constructor() {
        this.events = {};
    }

    /**
     * 
     * @param {String} name 
     * @param {Function} callback 
     * @returns {Function} 
     */
    on(name, callback) {
        if (!this.events[name]) {
            this.events[name] = [];
        }
        this.events[name].push(callback);

        return () => {
            this.off(name, callback);
        };
    }

    /**
     * 
     * @param {String} name 
     * @param {Function} callback  
     */
    off(name, callback) {
        if (this.events[name]) {
            if (typeof callback === 'undefined') {
                this.events[name] = [];
            } else {
                this.events[name] = this.events[name].filter(item => callback !== item);
            }
        }
    }

    /**
     * 
     * @param {String} name 
     * @param {Array} data 
     */
    trigger(name, ...data) {
        if (this.events[name]) {
            this.events[name].forEach(callback => callback(...data));
        }
    }
}
