import React, { useRef, useEffect, useState } from 'react';
import errorBoundary from '@src/components/errorBoundary';
import { Player } from '@ifeng/three_terminal/es/mobile/video';
// setH5PageTouchArea / setNoSlideArea  setPageNoActiveListener
import { setH5PageTouchArea, setNoSlideArea, setPageNoActiveListener } from '@src/public/utils/app';
// ios
import { isIOS, inFlowIframeJump } from '@src/public/utils';
// png
import DefaulVideo from './defaultVideo.png';
import styles from './index.css';
import PropTypes from 'prop-types';
// 
import { linkBase, dispatchPage } from '../Link';
// 
import { EVENT_LIST_INFLOW } from '@src/public/constants';

const TimeListVideo = ({
    videoConfig, // 
    playermsg, // 
    showType, // 
    timeId, // id
}) => {
    // 
    // const { url, poster, duration2 } = video;
    const { mobileUrl, bigPosterUrl, duration } = videoConfig;

    // 
    const [showTongcengVideo, setShowTongcengVideo] = useState(false);

    // id
    const id = Math.random();

    // uuid
    const uuid = Math.random();
    // uuid
    const [videoUuid, setVideoUuid] = useState('');
    const currentVideoUuid = useRef(''); // id(ios)
    const videoPlayingRef = useRef(false); // (ios)

    const playerRef = useRef(); // 
    const videoApis = useRef(); // 
    const videoPosition = useRef(); // 

    // id
    const videoDestory = uuid => {
        setVideoUuid(uuid);
    };

    // videoUuid => 
    useEffect(
        () => {
            // id
            if (!videoDestory || (videoUuid && videoUuid === currentVideoUuid.current)) {
                setShowTongcengVideo(true);
            } else {
                // 
                setShowTongcengVideo(false);
            }
        },
        [videoUuid],
    );
    // ios
    useEffect(
        () => {
            if (showTongcengVideo) {
                let index = 1;
                // :
                const callbackName = `callback_${index++}`;

                setTimeout(() => {
                    // 
                    // eslint-disable-next-line no-unused-expressions
                    window?.grounds?.openTongcengVideo &&
                        // url/JSON.stringify/callbackName
                        window?.grounds?.openTongcengVideo(
                            mobileUrl, // 
                            JSON.stringify({
                                // 
                                // containerId: `videoBox-${id}`, // id
                                containerId: `videoBox-${id}`, // id
                                ad: {},
                                autoplay: false, // {boolean}  false
                                poster: bigPosterUrl || DefaulVideo, // {string} 
                                zt: '', // id
                                dm: '', // 
                                lm: '', // 
                                keywords: '', // 
                                vid: '', // 
                                position: 'relative',
                            }),
                            callbackName, // web
                        );
                    // 
                    window[callbackName] = eventName => {
                        if (eventName === 'play' && !videoPlayingRef.current) {
                            console.log('playing :>> ', true);
                            videoPlayingRef.current = true;
                        }
                        if (['pause', 'end', 'error'].includes(eventName) && videoPlayingRef.current) {
                            console.log('playing :>> ', false);
                            videoPlayingRef.current = false;
                        }
                    };
                    // api
                    // eslint-disable-next-line no-unused-expressions
                    window.grounds?.playVideo && window.grounds?.playVideo();

                    return;
                }, 30);
            }
        },
        [showTongcengVideo],
    );

    // =(ios)
    useEffect(() => {
        setPageNoActiveListener(() => {
            // eslint-disable-next-line no-unused-expressions
            videoApis.current?.pause();
        });
    }, []);

    // =>ios
    useEffect(() => {
        if (playerRef.current) {
            const io = new IntersectionObserver( // [callback,option]
                ([entry]) => {
                    const { rootBounds, boundingClientRect, intersectionRatio } = entry;

                    if (intersectionRatio === 0 && videoPlayingRef.current === true) {
                        // 
                        // eslint-disable-next-line no-unused-expressions
                        window?.grounds?.destoryVideo();
                        setShowTongcengVideo(false);
                    }
                },
                { root: null, threshold: 0, rootMargin: '-163px 0px -65px 0px' },
            );

            // 
            io.observe(playerRef.current);

            // 
            return () => {
                io.disconnect();
            };
        }
    }, []);

    // player/
    const mobileVideoData = {
        // 
        config: {
            auth: true, // guidfalse
            showTitle: true, // 
            showPoster: true, // 
            showController: true, // 
            m3u8Play: true,
            pauseAfterLeave: true, // 
            singlePlay: true, // 
            isCircle: true,
            controlsList: 'nodownload',
            clickVideoStatisticsCB: () => {
                // console.log('');
            },
            firstFrameStatisticsCB: () => {
                // console.log('');
            },
            // 
            getExposeApisCb: apis => {
                videoApis.current = apis;
            },
            // 
            fullscreenListener: isFull => {
                if (isIOS()) {
                    return;
                }
                if (isFull) {
                    videoPosition.current = document.documentElement.scrollTop || document.body.scrollTop;
                } else {
                    window.scroll(0, videoPosition.current);
                }
            },
        },

        videoinfo: {
            mobileUrl, // mobileUrlguid
            bigPosterUrl, // bigPosterUrlguidbigPosterUrl
        },
    };

    return (
        <>
            {showType === EVENT_LIST_INFLOW && timeId ? (
                <div
                    className={styles.video_wrap}
                    onClick={() => {
                        // console.log('');
                        const url = `${linkBase}/loc/timeline/event/${timeId}`;

                        if (window.grounds) {
                            dispatchPage({ to: url });
                        } else {
                            inFlowIframeJump(url);
                        }
                    }}>
                    <div className={styles.video_img_container}>
                        <img src={bigPosterUrl || DefaulVideo} />
                        <span className={styles.video_play_icon} />
                    </div>
                </div>
            ) : (
                mobileUrl &&
                // ios
                (window?.grounds?.openTongcengVideo ? (
                    <div ref={playerRef} className={styles.video_wrap}>
                        {/*   */}
                        {showTongcengVideo ? (
                            <div className={styles.video_container}>
                                <div className={styles.floatVideoBox} id={`videoBox-${id}`}>
                                    <div style={{ width: '101%', height: '101%' }} />
                                </div>
                                <img src={bigPosterUrl || DefaulVideo} />
                            </div>
                        ) : (
                            // :showTongcengVideo
                            <div
                                className={styles.video_img_container}
                                onClick={() => {
                                    try {
                                        if (window?.grounds?.openTongcengVideo) {
                                            // eslint-disable-next-line no-unused-expressions
                                            window?.grounds?.destoryVideo();
                                            // 
                                            currentVideoUuid.current = uuid;
                                            // id
                                            // eslint-disable-next-line no-unused-expressions
                                            videoDestory && videoDestory(uuid);

                                            window.playVideo = (eventType, playName) => {
                                                console.log(eventType, playName);
                                            };
                                        }
                                    } catch (e) {
                                        console.log(e);
                                    }
                                }}>
                                <img src={bigPosterUrl || DefaulVideo} />
                                {!showTongcengVideo && <span className={styles.video_play_icon} />}
                            </div>
                        )}
                    </div>
                ) : (
                    // /
                    <div className={styles.video_wrap}>
                        <div className={styles.video_inner}>
                            <div
                                className={styles.video_content}
                                ref={playerRef}
                                onClick={() => {
                                    const { top, bottom } = playerRef.current?.getBoundingClientRect();

                                    setNoSlideArea([{ top, end: bottom, type: 'fixed' }]);
                                    setH5PageTouchArea(top, bottom, window.innerHeight);
                                }}>
                                <Player
                                    ref={playerRef}
                                    videoConfig={{ ...mobileVideoData.videoinfo }}
                                    playermsg={playermsg || { ...mobileVideoData.config }}
                                />
                            </div>
                        </div>
                    </div>
                ))
            )}
        </>
    );
};

TimeListVideo.propTypes = {
    /**
     * 
     */
    videoConfig: PropTypes.object,
    /**
     * 
     */
    playermsg: PropTypes.object || undefined,
    showType: PropTypes.string,
    timeId: PropTypes.string,
};

export default errorBoundary(TimeListVideo);
